import React from "react";
import styled from "styled-components";

const SearchHeader = styled.header`
	width: 100%;
	margin: 50px auto;
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: space-between;
`;

const HeaderTitle = styled.h1`
	color: ${(props) => (props.primary ? "red" : "rgb(247, 205, 70)")};
`;

const HeaderTitleSpan = styled.span`
	color: rgb(6, 42, 220);
`;

const SearchLabel = styled.label`
	font-size: 16px;
	margin-left: 20px;
	margin-top: 5px;
	display: block;
	transition: opacity 0.3s, visibility 0.3s, transform 0.3s;
`;

const SearchInput = styled.input`
	font-size: 16px;
	border: none;
	padding: 10px 20px;
	width: 300px;
	border-bottom: 3px solid red;
	transition: border-bottom-color 0.3s;
	&:focus {
		border-bottom-color: rgb(6, 42, 220);
	}
`;

function Header(props) {
	return (
		<SearchHeader>
			<HeaderTitle primary>
				Pokemon<HeaderTitleSpan>App</HeaderTitleSpan>
			</HeaderTitle>
			<div>
				<SearchInput
					onChange={props.handleChangeInput}
					value={props.value}
					type="text"
					placeholder="Search"
					autoComplete="off"
					id="search"
				/>
				<SearchLabel htmlFor="search"></SearchLabel>
			</div>
		</SearchHeader>
	);
}

export default Header;
