import React from "react";
import styled from "styled-components";
import Info from "../Info/Info";

const Section = styled.section`
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	align-items: center;
	justify-items: center;
	grid-gap: 25px;
`;

const Article = styled.article`
	display: flex;
	flex-direction: column;
	justify-content: center;
`;

const Header = styled.header`
	display: flex;
	align-items: center;
	justify-content: space-between;
`;

const BoldType = styled.span`
	font-weight: 700;
`;

function PokemonContainer({ filterPokemons }) {
	return (
		<Section>
			{filterPokemons.length ? (
				filterPokemons.map(
					({
						id,
						name,
						number,
						imageUrl,
						supertype,
						types,
						rarity,
					}) => {
						return (
							<Article key={id}>
								<Header className="container__header">
									<h2>{name}</h2>
									<p>nr.{number}</p>
								</Header>
								<img src={imageUrl} alt={name} />
								<p>
									<BoldType>Supertype: </BoldType>
									{supertype ? supertype : "-"}
								</p>
								{types ? (
									<p>
										<BoldType>Types: </BoldType>
										{types}
									</p>
								) : null}
								{rarity === "" ? null : (
									<p>
										<BoldType>Rarity:</BoldType>
										{rarity}
									</p>
								)}
							</Article>
						);
					}
				)
			) : (
				<div>
					<Info>There are no results</Info>
				</div>
			)}
		</Section>
	);
}

export default PokemonContainer;
