import React from "react";
import "./App.css";
import PokemonContainer from "./Components/PokemonContainer/PokemonContainer";
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";

let currentPage = 1;
let pageSize = 4;

class PokemonApp extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			pokemons: [],
			activeSpinner: true,
			value: "",
		};
	}

	handleChangeInput = (e) => {
		this.setState({
			value: e.target.value.substr(0, 20),
		});
	};

	hideSpinner = () => {
		this.setState({
			activeSpinner: false,
		});
	};

	handleClick = () => {
		this.hideSpinner();
		const url = `https://api.pokemontcg.io/v1/cards?page=${currentPage}&pageSize=${pageSize}`;

		fetch(url)
			.then((res) => {
				if (res.status !== 200) {
					throw Error;
				} else {
					return res.json();
				}
			})
			.then((data) => {
				const { cards } = data;
				this.setState({
					pokemons: [...this.state.pokemons, ...cards],
					activeSpinner: true,
				});
				currentPage++;
			})
			.catch((err) => console.log(err));
	};

	componentDidMount() {
		this.handleClick();
	}

	render() {
		const filterPokemons = this.state.pokemons.filter(({ name }) => {
			return (
				name
					.toLowerCase()
					.indexOf(this.state.value.toLowerCase()) !== -1
			);
		});

		console.log(filterPokemons);

		return (
			<div className="App">
				<Header
					value={this.state.value}
					handleChangeInput={this.handleChangeInput}
				/>
				<PokemonContainer filterPokemons={filterPokemons} />
				<Footer
					onClick={this.handleClick}
					active={this.state.activeSpinner}
				/>
			</div>
		);
	}
}

export default PokemonApp;
